.temp-wrapper {
  margin: 20px;
}

@media screen and (max-width: 487px) {
  .temp-wrapper .px__screen {
    display: none;
  }
  .temp-wrapper .px__screen .card {
    display: block;
    background-color: #000;
  }
}

@media all and (min-width: 480px) {
  .temp-wrapper2 {
    width: 50%;
    margin: 20px auto;
  }
  .px {
    margin-top: 6rem;
  }
  .temp-wrapper--wide {
    width: 100%;
  }

  .temp-wrapper--wider {
    width: 100%;
  }
}

@media all and (min-width: 768px) {
  .temp-wrapper {
    width: 33.33337%;
  }

  .temp-wrapper--wide {
    width: 66.66667%;
  }

  .temp-wrapper--wider {
    width: 100%;
  }
  .temp-wrapper2 {
    display: none;
  }
}

@media all and (min-width: 1024px) {
  .temp-wrapper {
    width: 25%;
  }

  .temp-wrapper--wide {
    width: 50%;
  }

  .temp-wrapper--wider {
    width: 75%;
  }
  .temp-wrapper2 {
    display: none;
  }
}

